@use 'sass:math'

@font-face
  font-family: Stardew Valley Stonks
  src: url("/public/Stardew Valley Stonks.otf")

@function ui-img($file)
  @return url("/public/img/UI/#{$file}.png")


$arrow-length: 1em

%arrow
  content: ""
  display: inline-block
  background-size: contain
  background-repeat: no-repeat
  background-position: center center
  width: $arrow-length
  height: $arrow-length

%arrow-left
  background-image: ui-img(Arrow Left)

%arrow-right
  background-image: ui-img(Arrow Right)

%arrow-up
  background-image: ui-img(Arrow Up)

%arrow-down
  background-image: ui-img(Arrow Down)

%season
  height: 1.25em

@each $season in Spring Summer Fall Winter
  .#{to-lower-case($season)}
    @extend %season
    content: url("/public/img/Seasons/#{$season}.png")

.icon
  width: 1.5em
  height: 1.5em

  &-processor
    width: 1.25em

  &-text
    display: inline-block
    width: max-content

    & > :first-child
      vertical-align: text-bottom
      margin-right: 0.375em

@mixin icon-img($name, $folder, $className: $name)
  .#{to-lower-case($className)}
    @extend .icon
    content: url("/public/img/#{$folder}/#{$name}.png")

@each $product in Jam Pickles Wine Juice
  @include icon-img($product, Products)

@each $profession in Tiller Artisan Agriculturist Gatherer Botanist
  @include icon-img($profession, Skills)

%skill
  width: 1.25em
  height: 1.25em

@each $skill in Farming Foraging
  .#{to-lower-case($skill)}
    @extend %skill
    content: url("/public/img/Skills/#{$skill}.png")

@mixin quality($quality, $color)
  .#{to-lower-case(#{$quality})}
    --color: #{$color}
    --img: url("/public/img/Qualities/#{$quality}.png")

@include quality(Iridium, purple)
@include quality(Gold, gold)
@include quality(Silver, silver)
@include quality(Normal, white)


%colon
  &::after
    font-weight: 600
    content: ":"

%button-unstyled
  padding: 0
  background: none
  outline: none
  box-shadow: none
  border: none
  color: black

$border-box-outset: 0.75rem

%border-box
  image-rendering: pixelated
  $img-scale: 4
  border-image-source: ui-img(Border Box)
  border-image-slice: ($img-scale * 4) fill
  border-image-width: 1rem
  border-image-outset: $border-box-outset
  margin: $border-box-outset

  & > *
    image-rendering: initial

%focus-outline
  outline: var(--border-thin) solid #d83700
  outline-offset: var(--border-thin)

:focus
  @extend %focus-outline

%hover-brightness
  filter: brightness(1.15)

%hover-scale
  transform: scale(1.1)


:root
  font-family: Stardew Valley Stonks, Nunito, sans
  line-height: 1.25
  word-spacing: 0.125em

body
  margin: 0
  min-width: min-content
  min-height: 100vh
  image-rendering: pixelated
  background-image: ui-img(Background)
  background-size: cover
  background-position: center center
  background-attachment: fixed
  overflow-y: scroll

  & > span // noscript
    text-align: center
    margin-top: 1rem

  & > *
    image-rendering: initial

header
  padding: 1.5em

  & > a
    display: flex
    flex-wrap: wrap
    align-items: center
    justify-content: center
    row-gap: 1em
    column-gap: 2em

    & > img
      height: 5rem

footer
  display: flex
  align-items: center
  justify-content: space-evenly
  text-align: center
  flex-wrap: wrap
  font-size: 1.25rem
  gap: 1em
  min-height: 15vh
  box-sizing: border-box
  padding: 1em

  & > span
    @extend %border-box
    padding: 0.625em 0.75em 0.5em 0.75em

img
  user-select: none

dl
  display: grid
  grid-template-columns: fit-content(13em) auto
  gap: 0.5em
  margin: 0

dt
  @extend %colon
  justify-self: end
  text-align: right

dd
  margin: 0
  justify-self: start

details
  & > summary
    list-style: none
    cursor: pointer
    user-select: none
    font-size: 1.5rem
    padding: 0.5em 0.5em 0.25em 0.5em

    &::marker,
    &::-webkit-details-marker
      display: none

    &::before
      @extend %arrow
      @extend %arrow-right
      margin-right: 0.5em

    &:hover::before
      @extend %hover-scale

    &:focus
      outline-offset: calc(-1 * var(--border-thin))

  & > div
    display: flex
    flex-direction: column
    row-gap: 0.5em
    padding: 0.5em

  &[open] > summary::before
    @extend %arrow-down
